.subtotal {
  z-index: inherit;
  /* display: flex; */
  flex-direction: column;
  justify-content: space-between;
  min-width: 200px;
  height: 100px;
  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
  margin-left: 20px;
}

.subtotal_gift {
  display: inline-flex;
  align-items: center;
}

.subtotal_gift > input {
  margin-right: 5px;
}

.subtotal > button {
  background: #ffd11d;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}
