.home {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
}

.home_row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
}

.home_image {
  width: 100%;
  z-index: -1;
  /* margin-bottom: -150px; */
  mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.4),
    rgba(0, 0, 0, 0.9)
  );
}

#bg_home {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
}
