.checkout {
  z-index: 1;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  height: max-content;
  align-items: flex-start;
}

.checkout_title {
  z-index: inherit;
  margin-right: auto;
  padding: 10px;
  padding-right: auto;
  border-bottom: 1px solid lightgray;
}

.checkout_container {
  /* z-index: 1; */
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
}

.checkout_right {
  padding-top: 10px;
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
  margin-left: auto;
}

#bg_checkout {
  width: 100%;
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100%;
  /* margin-bottom: -150px; */
  mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.3),
    rgba(0, 0, 0, 0.8)
  );
  /* max-width: 1500px; */
}
