.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin: 10px;
    padding: 20px;
    padding-top: 40px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    z-index: 1;
    /* background-color: lightgrey; */
}

.product > img {
    max-height: 300px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
    /* margin-top: 2%; */
}

.product > button {
    background: #ffd11d;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.product_price {
    margin-top: 5px;
}

.product_info {
    height: 100px;
    /* margin-bottom: 10px; */
}

.product_rating > p {
    /* display: flex; */ 
    padding: 0px 10px;
    margin-top: 10px;
}

.product_rating {
    display: flex;
    margin-bottom: 10px;
}
